import React, { ChangeEvent } from "react";
import { useSelector, useDispatch, connect } from "react-redux"; //  useSelector  useDispatch
import { addPostAC, updatePostTextAC } from "../../../redux/Profile-reducer"; //  
import { AppDispatch, RootState } from "../../../redux/redux-store"; //  RootState
import { Post } from "./Post/Post";
import { MyPosts } from "./MyPosts";

const mapStateToProps = (state: RootState) => {
  return {
    postText: state.profilePage.profilePage.postText, //  text  
    posts: state.profilePage.profilePage.posts, //     
  };
};

//   mapDispatchToProps
type mapDispatchToPropsType = {
  handleTextChange: (newText: string) => void;
  handleAddPost: (postText: string) => void; //   ,     postText
};

// mapDispatchToProps   
const mapDispatchToProps = (dispatch: AppDispatch): mapDispatchToPropsType => {
  return {
    handleTextChange: (text: string) => dispatch(updatePostTextAC(text)), //  
    handleAddPost: (postText: string) => {
      if (postText.trim() !== "") {
        dispatch(addPostAC(postText)); //        
        dispatch(updatePostTextAC("")); //    
      }
    },
  };
};

//   MyPosts     Redux
const MyPostsContainer = connect(mapStateToProps, mapDispatchToProps)(MyPosts);

export default MyPostsContainer;
